import React, { useState, useEffect } from "react";
import {
  Container,
  Typography,
  Divider,
  IconButton,
  Snackbar,
  SnackbarContent,
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  Tooltip,
} from "@material-ui/core";
import { connect } from "react-redux";
import moment from "moment";
import { withRouter } from "react-router-dom";
import { Close, Delete } from "@material-ui/icons";
import { red, green } from "@material-ui/core/colors";
import api from "../api/api";
import { logout } from "../actions";

const OrderHistory = (props) => {
  const { logout, profile } = props;
  const [productOrder, setProductOrder] = useState([]);
  const [toast, settoast] = useState(false);
  const [message, setmessage] = useState(null);
  const [status, setstatus] = useState(null);
  useEffect(() => {
    document.title = "Order History";
    if (Object.keys(profile).length) fetchProductOrders();
  }, [Object.keys(profile).length]);

  const fetchProductOrders = async () => {
    if (localStorage.getItem("mom-customer")) {
      try {
        const response = await api.get(`/api/product-order/${profile.id}`, {
          headers: {
            Authorization: "Bearer " + localStorage.getItem("mom-customer"),
          },
        });
        return setProductOrder(response.data.data);
      } catch (err) {
        return;
      }
    } else {
      return logout();
    }
  };

  const deleteOrder = async (id) => {
    if (localStorage.getItem("mom-customer")) {
      try {
        await api.delete(`/api/product-order/${id}`, {
          headers: {
            Authorization: "Bearer " + localStorage.getItem("mom-customer"),
          },
        });
        setProductOrder(productOrder.filter((a) => a.id !== id));

        setstatus(green[700]);
        setmessage("Order Deleted Successfully");
        return settoast(true);
      } catch (err) {
        setstatus(red[700]);
        setmessage("Something went wrong. Try again");
        return settoast(true);
      }
    } else {
      return logout();
    }
  };

  const renderList = () => {
    if (productOrder.length) {
      return (
        <Table size="small">
          <TableHead>
            <TableRow>
              <TableCell>Product</TableCell>
              <TableCell align="center">QTY</TableCell>
              <TableCell align="center">Status</TableCell>
              <TableCell colSpan={2}>Order Date</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {productOrder.map((a, index) => {
              return (
                <TableRow key={index}>
                  <TableCell>
                    <Typography>{a.name}</Typography>
                  </TableCell>
                  <TableCell align="center">
                    <Typography>{a.type === 1 ? "-" : a.quantity}</Typography>
                  </TableCell>
                  <TableCell>
                    {
                      a.status && <Typography align="center" style={{ color: a.status === 1 ? '#fe7517' : a.status === 2 ? '#07800b' : a.status === 3 ? '#07800b' : "#07800b" }}>{a.status === 1 ? 'Pending' : a.status === 2 ? 'Confirmed' : a.status === 3 ? 'Delivered' : null}</Typography>
                    }
                  </TableCell>
                  <TableCell>
                    <Typography>
                      {moment(a.created).format("DD-MM-YYYY")}
                    </Typography>
                  </TableCell>
                  <TableCell>
                    <Tooltip title="Delete">
                      <IconButton
                        disabled={a.status === 1 ? false : true}
                        size="small"
                        onClick={() => {
                          if (
                            window.confirm(
                              `Are you sure you want to cancel this order? You cannot recover once cancelled!`
                            )
                          ) {
                            return deleteOrder(a.id);
                          }
                        }}
                      >
                        Cancel Order
                      </IconButton>
                    </Tooltip>
                  </TableCell>
                </TableRow>
              );
            })}
          </TableBody>
        </Table>
      );
    } else {
      return (
        <Typography align="center" variant="h6" style={{ marginTop: 100 }}>
          You have no order history
        </Typography>
      );
    }
  };

  return (
    <Container style={{ marginBottom: 380 }}>
      <Typography variant="h6">My Order History</Typography>
      <Divider style={{ marginTop: 20 }} />
      {renderList()}

      <Snackbar
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
        open={toast}
        autoHideDuration={3000}
        onClose={() => {
          settoast(false);
        }}
      >
        <SnackbarContent
          style={{ backgroundColor: status }}
          message={message}
          onClose={() => {
            settoast(false);
          }}
          action={
            <IconButton
              key="close"
              color="inherit"
              onClick={() => settoast(false)}
            >
              <Close />
            </IconButton>
          }
        ></SnackbarContent>
      </Snackbar>
    </Container>
  );
};

const mapstatetoprops = (state) => {
  return { profile: state.profile };
};

export default connect(mapstatetoprops, { logout })(withRouter(OrderHistory));
